import { createMuiTheme } from '@material-ui/core/styles';

const madidsBlue = '#3a82c3';
const madidsYellow = '#ffcc07';
const white = '#E2F4FD';

export default createMuiTheme({
  overrides: {
    MuiMenuItem: {
      root: {
        color: props => (props.disabled ? 'white' : 'white')
      }
    }
  },
  palette: {
    common: {
      blue: `${madidsBlue}`,
      yellow: `${madidsYellow}`,
      white: `${white}`
    },
    primary: { main: `${madidsBlue}` },
    secondary: { main: `${white}` }
  },
  typography: {
    h3: {
      fontWeight: 300,
      color: white
    },
    tab: {
      textTransform: 'none',
      fontWeight: '500',
      fontSize: '1.15rem'
    },
    bookNow: {
      fontSize: '1rem',
      textTransform: 'none',
      color: 'white'
    },
    globalBtn: {
      color: white,
      textTransform: 'none',
      fontFamily: 'Roboto'
    }
  }
});
