import React from 'react';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/styles';
import {Link} from 'react-router-dom';

const useStyles = makeStyles(theme => ({
  ...theme.typography.globalBtn,
  btnOutline: {
    '&:focus' : {
      backgroundColor: theme.palette.common.white,
      color: theme.palette.common.blue
    }
  }
}))

const GButtonOne = (props) => {
  const classes = useStyles();

  return (
    <Button style={props.style} component={Link} to={props.navTo} className={classes.btnOutline} color='secondary' variant='outlined' size='large'>{props.title}</Button>
  );
};

export default GButtonOne;