import React, { useEffect } from 'react';
import { Slider, Slide, Caption, Button } from 'react-materialize';
import 'materialize-css/dist/css/materialize.css';
import M from 'materialize-css';
import heroImage1 from '../../assets/img/desktop/resort1.jpg';
// import mobileImage1 from '../../assets/img/mobile/1.jpg';
import heroImage2 from '../../assets/img/desktop/1.jpg';
import heroImage3 from '../../assets/img/desktop/resort3.jpg';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles(theme => ({
  sliderWrap: {
    position: 'relative',
    height: '100vh',
    width: '100vw',
  }
}));

const HeroSlider = () => {
  const classes = useStyles();

  var elems = document.querySelectorAll('.slider');
  useEffect(() => {
    
    // eslint-disable-next-line no-unused-vars
    M.Slider.init(elems);
  });

  return (
    <Grid container>
      <Grid item className={classes.sliderWrap}>
          <Slider fullscreen>
            <Slide image={<img alt='' src={heroImage1} />}>
              <Caption placement='center'>
                <h3>Take Your Dream Vacation </h3>
                <h5 className='light grey-text text-lighten-3'>
                  Experience the island`s exciting water adventures or just
                  settle for <br />a comfortable island living set, within the
                  magic and beauty of Boracay
                </h5>
                <Button flat node='button' large waves='light'>
                  Learn More
                </Button>
              </Caption>
            </Slide>
            <Slide image={<img alt='' src={heroImage2} />}>
              <Caption placement='left'>
                <h3>We Work With All Budgets</h3>
                <h5 className='light grey-text text-lighten-3'>
                  We showcase clean and beautiful rooms, so go ahead <br />
                  and choose according to your preference and budget.
                </h5>
                <Button flat node='button' large waves='light'>
                  Learn More
                </Button>
              </Caption>
            </Slide>
            <Slide image={<img alt='' src={heroImage3} />}>
              <Caption placement='right'>
                <h3>Dinner Dates</h3>
                <h5 className='light grey-text text-lighten-3'>
                  Celebrate your love on your next stay with us! Reserve a
                  romantic <br /> dinner for two and try the best local
                  delicacies Boracay has to offer.
                </h5>
                <Button flat node='button' large waves='light'>
                  Learn More
                </Button>
              </Caption>
            </Slide>
          </Slider>
      </Grid>
    </Grid>
  );
};

export default HeroSlider;

