import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/styles';
import Typography from '@material-ui/core/Typography';
import GButtonOne from './ui/GButtonOne';

const useStyles = makeStyles( theme => ({
  slot: {
    backgroundImage: 'linear-gradient(to right, rgba(58, 130, 195,1), rgba(3, 180, 228, 1), rgba(58, 130, 195, 1))',
    height: '15em',
  },
  slotH3: {
    ...theme.typography.h3,
    [theme.breakpoints.down('sm')]: {
      fontSize: '2rem'
    }
  }
}))


const SlotComponent = () => {
  const btnTitle = 'Contact Us'
  const navTo = '/contact'

  const classes = useStyles();
  

  return (
    <Grid align='center' container className={classes.slot} alignItems='center' justify='center'>
      <Grid item>
        <Typography className={classes.slotH3} variant='h3'>Planning on your next Boracay trip?</Typography>
        <GButtonOne style={{marginTop: '1em'}} title={btnTitle} navTo={navTo}/>
      </Grid>
    </Grid>
  );
};

export default SlotComponent;