import React from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import cave from '../assets/img/desktop/cave1.jpg';
import caveMobile from '../assets/img/mobile/cave1.jpg'
import GButtonOne from './ui/GButtonOne';

const useStyles = makeStyles(theme => ({
  background: {
    backgroundImage: `url(${cave})`,
    backgroundPosition: 'center',
    backgroundSize: ' cover',
    backgroundAttachment: 'fixed',
    backgroundRepeat: 'no-repeat',
    width: '100%',
    height: '50vh',
    [theme.breakpoints.down('md')] : {
      backgroundImage: `url(${caveMobile})`,
      backgroundAttachment: 'inherit'
    }
  },
  ctaSectionH2: {
    ...theme.typography.h3,
    [theme.breakpoints.down('sm')] : {
      fontSize: '2.25rem',
    }
  },
  ctaSectionSub: {
    color: '#ffffff',
    [theme.breakpoints.down('sm')] : {
      paddingLeft: '5em',
      paddingRight: '5em'
    }
  }
  
}));

const CtaSection = () => {
  const btnTitle = 'Book Now!'
  const navTo = '/booking'

  const classes = useStyles();
  return (
    <Grid
      alignItems='center'
      justify='space-between'
      container
      className={classes.background}>
      <Grid item style={{ width: '100%' }}>
        <Grid container direction='column' justify='center' alignItems='center' spacing={4}>
          <Typography className={classes.ctaSectionH2} variant='h2'>
            Ready to take the plunge?
          </Typography>
          <Typography className={classes.ctaSectionSub} variant='subtitle1'>
            Go ahead and wash your stress away, start your next adventure with us here in Boracay.
          </Typography>
          <Grid item>
            <GButtonOne title={btnTitle} navTo={navTo}/>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
};

export default CtaSection;
