import React from 'react';
import Grid from '@material-ui/core/Grid';

import { makeStyles } from '@material-ui/styles';
import logo from '../../assets/img/logo.svg';
import facebook from '../../assets/img/facebook.svg';
import instagram from '../../assets/img/instagram.svg';
import twitter from '../../assets/img/twitter.svg';
import { Link } from 'react-router-dom';

const useStyles = makeStyles(theme => ({
  footer: {
    backgroundColor: theme.palette.common.blue,
    width: '100%',
    zIndex: 1302,
    position: 'relative'
  },
  footerContainer: {
    backgroundColor: theme.palette.common.blue,
    height: '15em'
  },
  footerLogo: {
    height: '4em',
    margin: 0,
    padding: 0
  },
  footnote: {
    color: theme.palette.common.white,
    lineHeight: 0.75,
    fontSize: '.75rem',
    margin: 0,
    padding: '.25em',
    letterSpacing: '1px'
  },
  footnoteItems: {
    marginTop: '2em'
  },
  footnoteItem: {
    // fontWeight: "bold",
    letterSpacing: '1px',
    color: theme.palette.common.yellow
  },
  icon: {
    height: '1.5em'
  },
  link: {
    color: 'white',
    fontFamily: 'Arial',
    fontSize: '0.75rem',
    fontWeight: 'bold',
    textDecoration: 'none'
  }
}));

const Footer = props => {
  const { setValue } = props;
  const classes = useStyles();
  return (
    <footer className={classes.footer}>
      <Grid
        container
        className={classes.footerContainer}
        direction='column'
        justify='space-around'
        alignItems='center'>
        <Grid item className={classes.gridItem}>
          <Grid container spacing={2} className={classes.footnoteItems}>
            <Grid
              item
              component={Link}
              to='/'
              className={classes.link}
              onClick={() => {
                setValue(0);
              }}>
              Home
            </Grid>
            <Grid
              component={Link}
              to='/rooms'
              item
              className={classes.link}
              onClick={() => {
                setValue(1);
              }}>
              Rooms
            </Grid>
            <Grid
              component={Link}
              to='/gallery'
              item
              className={classes.link}
              onClick={() => {
                setValue(2);
              }}>
              Gallery
            </Grid>
            <Grid
              component={Link}
              to='/contact'
              item
              className={classes.link}
              onClick={() => {
                setValue(3);
              }}>
              Contact
            </Grid>
            <Grid
              component={Link}
              to='/booking'
              item
              className={classes.link}
              onClick={() => {
                setValue(4);
              }}>
              Booking
            </Grid>
          </Grid>
        </Grid>
        <Grid item>
          <Grid container spacing={2}>
            <Grid
              item
              component={'a'}
              href='https://www.facebook.com'
              rel='noopener noreferrer'
              target='_blank'>
              <img
                alt='facebook logo'
                src={facebook}
                className={classes.icon}
              />
            </Grid>
            <Grid
              item
              component={'a'}
              href='https://www.twitter.com'
              rel='noopener noreferrer'
              target='_blank'>
              <img alt='twitter logo' src={twitter} className={classes.icon} />
            </Grid>
            <Grid
              item
              component={'a'}
              href='https://www.instagram.com'
              rel='noopener noreferrer'
              target='_blank'>
              <img
                alt='instagram logo'
                src={instagram}
                className={classes.icon}
              />
            </Grid>
          </Grid>
        </Grid>
        <Grid item align='center'>
          <img className={classes.footerLogo} alt='' src={logo} />
          <p className={classes.footnote}>2020 All Rights Reserved</p>
        </Grid>
      </Grid>
    </footer>
  );
};

export default Footer;
