import React from 'react';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import waves from '../assets/img/bank-note.svg';
import CardComponent from './Card';
import bed from '../assets/img/services/bed.svg';
import wifi from '../assets/img/services/wifi.svg';
import dine from '../assets/img/services/cutlery.svg';
import pwd from '../assets/img/services/blind.svg';
import bday from '../assets/img/services/birthday-cake.svg';
import shower from '../assets/img/services/shower.svg';
import cart from '../assets/img/services/shopping-cart.svg';
import creditCard from '../assets/img/services/credit-card.svg';
import safety from '../assets/img/services/fire-extinguisher.svg';

const useStyles = makeStyles(theme => ({
  guttersSpacing: {
    paddingLeft: '15em',
    paddingRight: '15em',
    paddingTop: '2em',
    paddingBottom: '4em',
    [theme.breakpoints.down('md')]: {
      paddingLeft: '5em',
      paddingRight: '5em'
    }
  },
  srvSection: {
    height: '100%',
    backgroundColor: '#f1f4f7',
    backgroundImage: `url(${waves})`
  },

  media: {
    height: '10em'
  }

}));



const ServicesSection = () => {
  const cardInfo = [{
    id: 1,
    icon: bed,
    title: 'Nice Rooms',
    description: 'We offer Modern & Elegant rooms, with trained attendants to assist you with your every hotel needs.'
  },
  {
    id: 2,
    icon: wifi,
    title: 'Internet Access',
    description: 'Never miss your favorite Netflix movie, Mobile games or Youtube channel because we have 24/7 hi-speed wifi.'
  },
  {
    id: 3,
    icon: dine,
    title: 'Wine & Dine',
    description: 'Try and savor the taste of the best local sweets and delicacies that our local folks offer here in Boracay.'
  },
  {
    id: 4,
    icon: cart,
    title: 'Accessibility',
    description: 'Our location is very near shopping and grocery malls, in fact they are just walking distance away.'
  },
  {
    id: 5,
    icon: shower,
    title: 'Water Supply',
    description: 'We have 24/hr hot or cold water uninterrupted water supply. So you can take a shower anytime you want.'
  },
  {
    id: 6,
    icon: pwd,
    title: 'PWD',
    description: 'We make sure that all our customers are well taken cared for specially the disabled or the old and elderly.'
  },
  {
    id: 7,
    icon: creditCard,
    title: 'Secured Payment',
    description: 'We make sure that all money transactions are secured. We accept all kinds of debit/credit card payments.'
  },
  {
    id: 8,
    icon: bday,
    title: 'Party',
    description: 'We can connect you to our partners if you are planning to have a party or a wild ocean adventure.'
  },
  {
    id: 9,
    icon: safety,
    title: 'Safety',
    description: 'We here at Madid\'s prioritize the safety of our visitors. Our building has been certified and cleared by the LGU safety board.'
  }
];

  const classes = useStyles();


  return (
    <Grid container className={`${classes.srvSection} ${classes.guttersSpacing}`} spacing={6}>
        {cardInfo.map(({id, ...props}) => (
          <Grid item key={id} md={4} sm={6}>
            <CardComponent {...props}/>
          </Grid>
        ))}
    </Grid> 
  );
};

export default ServicesSection;
